.settingsWrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 50%;
    background-color: rgba(126, 210, 255, 0.2);
}

.settingsBlock {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 90%;
    height: 100%;
}

.settingsBlock h2 {
    color: #2B2B2B;
    font-size: 30px;
    margin: 30px 0;
}

.textareaStyle {
    border: 2px solid #BBBBBB;
    border-radius: 5px;
    width: 100%;
    height: 9%;
    min-height: 40px;
    margin: 15px 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-size: 16px;
    padding: 6px 15px;
}

.dropAreaBlock {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    min-height: 150px;
    height: 20%;
    padding: 15px;
    border: 2px solid #BBBBBB;
    border-radius: 5px;
}

.dropArea {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 65%;
    border: 3px dashed #1975D1;
    border-radius: 5px;
}

.buttonsBlock {
    display: flex;
}

.buttonsBlock button {
    margin-right: 15px
}

@media screen and (max-width: 500px) {
    .settingsWrapper {
       width: 100%;
    }
}
